<!-- 首页-精品歌单 -->
<template>
    <div class="boutique-playList home-card">
        <TitleBar titleBarName="精品歌单" rightText="更多" />
        <div class="list home-card-swiper">
            <div class="swiper-container boutique-playList-swiper">
                <div class="swiper-wrapper">
                    <router-link
                        class="swiper-slide"
                        v-for="(item, id) in list"
                        :key="id"
                        :to="{ path: '/playlist', query: { id: item.id } }"
                    >
                        <img v-lazy="item.coverImgUrl" />
                        <span class="name">{{ item.name }}</span>
                        <PlayCount :playCount="item.playCount" :point="1" />
                    </router-link>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import { reactive, toRefs, onMounted, onUpdated } from "vue";
import { getHighQualityPlayList } from "@/api/home/index.js";
import { Swiper } from "swiper";
import PlayCount from "@/components/PlayCount.vue";
import TitleBar from "@/components/TitleBar.vue";
import { useStore } from "vuex";

export default {
    name: "BoutiquePlayList",
    components: { PlayCount, TitleBar },
    setup() {
        const state = reactive({
            titleBarName: "",
            list: [],
        });

        onMounted(() => {
            getHighQualityPlayList().then((res) => {
                state.list = res.data.playlists;
            });
        });

        onUpdated(() => {
            new Swiper(".boutique-playList-swiper", {
                slidesPerView: 3,
                spaceBetween: 14,
            });
        });

        return { ...toRefs(state) };
    },
};
</script>

<style scoped lang='scss'>
.boutique-playList {
    .list {
        .swiper-container {
            padding-right: 24px;
            .swiper-wrapper {
                .swiper-slide {
                    position: relative;
                    display: flex;
                    flex-direction: column;
                    border-radius: 10px;
                    img {
                        width: 100%;
                        margin-bottom: 4px;
                        border-radius: 10px;
                    }
                    .name {
                        @include ellipsis2();
                    }
                }
            }
        }
    }
}
</style>
